import { useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { Link, useParams } from "react-router-dom";

export const LoginView = ({ onLoggedIn }) => {
	const [username, setUsername] = useState("");
	const [password, setPassword] = useState("");
	// create form logic
	const handleSubmit = (event) => {
		// this prevents the default behavior of the form which is to reload the entire page
		event.preventDefault();

		const data = {
			username: username,
			password: password,
		};

		// call login endpoint from api
		fetch("https://myflix-movies-api.herokuapp.com/login/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(data),
		})
			.then((response) => response.json())
			.then((data) => {
				console.log("Login response: ", data);
				if (data.user) {
					// local storage on browser used to store user and token to persist session
					localStorage.setItem("user", JSON.stringify(data));
					localStorage.setItem("token", data.token);
					onLoggedIn(data.user, data.token);
				} else {
					alert("No such user");
				}
			})
			.catch((e) => {
				console.log(e);
				alert("Something went wrong");
			});
	};

	return (
		// create form
		<Form onSubmit={handleSubmit} className="mt-3 mb-5 text">
			<Form.Group>
				<Form.Label as="h2">🔒Login</Form.Label>
				<Form.Label>Username:</Form.Label>
				<Form.Control
					type="text"
					value={username}
					onChange={(e) => setUsername(e.target.value)}
					required
					minLength="3"
					placeholder="Enter username"
					style={{ color: "black", backgroundColor: "azure" }}
				/>
			</Form.Group>
			<Form.Group className="mb-2">
				<Form.Label>Password:</Form.Label>
				<Form.Control
					type="password"
					value={password}
					onChange={(e) => setPassword(e.target.value)}
					required
					style={{ color: "black", backgroundColor: "azure" }}
				/>
			</Form.Group>
			<Button variant="primary" type="submit">
				Log in
			</Button>
		</Form>
	);
};
